import {useDispatch, useSelector} from "react-redux";
import {setMas} from './redux/taskslice'
import {setDeleted} from "./redux/deletedSlice";
import Deleted from "./deleted";
import {Box, Chip, List, ListItem, ListItemButton} from "@mui/joy";

import {
    CancelRounded,
    TaskAltRounded
} from "@mui/icons-material";
import s from './tasks.module.css'
import {useContext} from "react";
import {ContextOpen} from "./App";


const Tasks = () => {

    const buttonOpen = useContext(ContextOpen)

    const dispatch = useDispatch()

    const sel = useSelector(state => state.tasks.value)

    const deleted = useSelector(state => state.deleted.deleted)

    const sss = sel.map((el, i) =>


        <div key={i}>


            <Box component="nav" aria-label="My site">
                <List
                    role="menubar"
                    row
                    sx={{
                        minWidth: 320,
                    }}
                >
                    <ListItem role="none">
                        <ListItemButton
                            role="menuitem"
                            component="a"
                            href="#horizontal-list"
                            aria-label="Home"
                        >

                        </ListItemButton>
                    </ListItem>
                    <ListItem role="none">
                        <ListItemButton className={s.sex}>
                            {el}
                        </ListItemButton>
                    </ListItem>

                    <ListItem role="none" sx={{ marginInlineStart: 'auto' }}>
                        <ListItemButton
                            role="menuitem"
                            component="a"
                            href="#horizontal-list"
                            aria-label="Delete"
                        >
                            <Chip onClick={() => {
                                let a = [...sel]
                                let b = sel.indexOf(el)
                                a.splice(b, 1)
                                dispatch(setMas(a))
                                dispatch(setDeleted(el))
                            }} variant="soft" color = 'danger' endDecorator={<CancelRounded color = 'danger' variant="plain" onClick={() => {
                                let a = [...sel]
                                let b = sel.indexOf(el)
                                a.splice(b, 1)
                                dispatch(setMas(a))
                                dispatch(setDeleted(el))
                            }}/>}>
                                Remove
                            </Chip>
                        </ListItemButton>
                    </ListItem>
                </List>
            </Box>


        </div>)


    return (<div>
        <ul className={s.ul}>
            {buttonOpen && sss}

        </ul>
        <Deleted/>

        <Chip variant="soft" color = 'success' startDecorator={<TaskAltRounded />}>
            {`Tasks Completed : ${deleted.length}`}
        </Chip>
        <Chip className={s.sum} variant="soft" color = 'danger' startDecorator={<CancelRounded />}>
            {`Tasks Remained : ${sel.length}`}
        </Chip>
    </div>)
}

export default Tasks
